*:focus{
  outline: 0;
}

body {
  background-image: url(/react_magic_paper/static/media/orb.5ac0f6af.png);
  background-size: cover;
  background-color: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 14px;
}

body::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to bottom right, #8A673F, #B7481F);
  opacity: .6;
  height: 700px;
  z-index: -1;
}

button {
  background-color: #B7481F;
  border: 0;
  border-radius: 5px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.08);
  color: #fafafa;
  padding: 16px;
}

.slide-in {
  -webkit-animation: slide-in 1s;
          animation: slide-in 1s;
}

.shake {
  -webkit-animation: shake .5s;
          animation: shake .5s;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

@-webkit-keyframes slide-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  to {
    opacity: 1;
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

.Card {
  background-color: #fafafa;
  border-radius: 5px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.08);
  color: #B7481F;
  padding: 30px;
}

.Magic__symbols {
  counter-reset: magic-symbol;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.Magic__symbol {
  border: 1px solid #B7481F;
  padding: 12px;
  width: 13%;
}

.Magic__symbol__img {
  width: 40px;
  height: 40px;
}

.Magic__symbol::before {
  counter-increment: magic-symbol;
  content: counter(magic-symbol) ' ';
  font-size: 20px;
  font-weight: 700;
}

.App {
  color: #fff;
  padding: 16px;
  text-align: center;
}

.App__header {
  margin-bottom: 100px;
}

.App__header-title {
  font-weight: 900;
  text-transform: uppercase;
  margin-bottom: 2px;
}

.App__header-tag {
  margin-top: 0;
  margin-bottom: 50px;
}

.App__footer {
  margin-top: 100px;
}

.App__footer-link {
  color: #f1f1f1;
}

@media (min-width: 768px) {
  .App__header-title {
    font-size: 50px;
  }
}

